import GoTop from '@inotom/vue-go-top';
import axios from 'axios';
export default {
  data: function data() {
    return {};
  },
  components: {
    GoTop: GoTop
  },
  computed: {
    currentIndex: function currentIndex() {
      return this.$store.getters.index;
    },
    isMobile: function isMobile() {
      return this.$store.getters.getIsMobile;
    },
    serverConfig: function serverConfig() {
      return this.$store.getters.getServerConfig;
    }
  },
  beforeDestroy: function beforeDestroy() {
    if (typeof window !== 'undefined') {
      window.removeEventListener('resize', this.onResize, {
        passive: true
      });
    }
  },
  mounted: function mounted() {
    this.onResize();
    window.addEventListener('resize', this.onResize, {
      passive: true
    });
  },
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var current = vm.$router.history.current;
      var newQuery = current.query;
      var filter = {
        serverNo: 'BOOKING_CONFIG'
      };
      vm.$store.dispatch('getServerConfig', filter).then(function (result) {
        try {
          var config = JSON.parse(result.configs);
          vm.$store.commit('setServerConfig', config);
          console.log('serverConfig', vm.serverConfig);
        } catch (error) {}
      }).catch(function (reject) {
        var config = '';
        vm.$store.commit('setServerConfig', config);
      });
    });
  },
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      var currentParams = newRoute.params;
      var currentQuery = newRoute.query;
    }
  },
  methods: {
    onResize: function onResize() {
      var vm = this;
      var isMobile = window.innerWidth < 1024;
      vm.$store.commit('setIsMobile', isMobile);
    }
  }
};