import Vue from 'vue';
import $ from 'jquery';
import toastr from 'toastr';
import support from '../../store/support.json';
Vue.use(toastr);
export default {
  props: [],
  components: {},
  data: function data() {
    return {
      groupId: window.themeDisplay ? window.themeDisplay.getScopeGroupId() : '',
      activeCalling: true,
      called: true,
      currentCalling: '',
      loadData: false,
      idVoicePortlet: 161427,
      queueList: [],
      headers: [{
        text: 'TT',
        align: 'center',
        sortable: false,
        class: 'py-3'
      }, {
        text: 'MÃ TK/BN',
        align: 'center',
        sortable: false,
        class: 'py-3'
      }, {
        text: 'HỌ TÊN',
        align: 'center',
        sortable: false,
        class: 'py-3'
      }, {
        text: 'QUẦY SỐ',
        align: 'center',
        sortable: false,
        class: 'py-3'
      }],
      gateNumberList: [],
      bookingList: []
    };
  },
  computed: {
    isMobile: function isMobile() {
      return this.$store.getters.getIsMobile;
    }
  },
  created: function created() {
    var vm = this;
    $('header').css('display', 'none');
    $('#banner').css('display', 'none');
    $('.navbar-container').css('display', 'none');
    $('#footer').css('display', 'none');
    vm.$nextTick(function () {
      var current = vm.$router.history.current;
      var currentQuery = current.query;
      setTimeout(function () {
        $('#footer').css('display', 'none');
      }, 500);
      vm.gateNumberList = [];

      if (currentQuery.hasOwnProperty('gate') && currentQuery.gate) {
        var gate = currentQuery.gate.split(',');

        for (var index in gate) {
          vm.gateNumberList.push({
            gateNumber: gate[index],
            bookings: {
              codeNumber: '',
              bookingName: ''
            }
          });
        }
      }
    });
    vm.getDanhSachCho();
  },
  updated: function updated() {
    var vm = this;
    $('header').css('display', 'none');
    $('#banner').css('display', 'none');
    $('.navbar-container').css('display', 'none');
    $('#footer').css('display', 'none');
    setTimeout(function () {
      $('#footer').css('display', 'none');
    }, 500);
    vm.$nextTick(function () {});
  },
  watch: {
    loadData: function loadData(val) {
      var vm = this;
      setTimeout(function () {
        vm.getDanhSachCho();
      }, 5000);
    },
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      var currentParams = newRoute.params;
      var currentQuery = newRoute.query;
      vm.gateNumberList = [];

      if (currentQuery.hasOwnProperty('gate') && currentQuery.gate) {
        var gate = currentQuery.gate.split(',');

        for (var index in gate) {
          vm.gateNumberList.push({
            gateNumber: gate[index],
            bookings: {
              codeNumber: '',
              bookingName: ''
            }
          });
        }

        vm.getDanhSachCho();
      }
    }
  },
  methods: {
    getDanhSachCho: function getDanhSachCho() {
      var vm = this;
      var currentQuery = vm.$router.history.current.query;
      var filterEform = {
        state: 2,
        className: 'EFORM'
      };
      var bookingDossier = '';
      var bookingEform = '';
      var count = 0;
      vm.$store.dispatch('getBookingDangGoi', filterEform).then(function (result) {
        count += 1;

        if (result) {
          bookingEform = result;
        } else {
          bookingEform = [];
        } // console.log('count', count)


        if (count === 2) {
          vm.mergeBooking(bookingDossier, bookingEform);
        }
      }).catch(function (reject) {
        count += 1;
        bookingEform = []; // console.log('count', count)

        if (count === 2) {
          vm.mergeBooking(bookingDossier, bookingEform);
        }
      });
      var filterDossier = {
        state: 2,
        className: 'DOSSIER'
      };
      vm.$store.dispatch('getBookingDangGoi', filterDossier).then(function (result) {
        count += 1;

        if (result) {
          bookingDossier = result;
        } else {
          bookingDossier = [];
        } // console.log('count', count)


        if (count === 2) {
          vm.mergeBooking(bookingDossier, bookingEform);
        }
      }).catch(function (reject) {
        count += 1;
        bookingDossier = []; // console.log('count', count)

        if (count === 2) {
          vm.mergeBooking(bookingDossier, bookingEform);
        }
      });
    },
    mergeBooking: function mergeBooking(bookingEform, bookingDossier) {
      var vm = this;
      vm.queueList = [];

      if (bookingEform.length > 0 || bookingDossier.length > 0) {
        vm.bookingList = bookingEform.concat(bookingDossier);

        var sortBooking = function sortBooking(bookingList) {
          function compare(a, b) {
            if (a.modifiedDate > b.modifiedDate) return -1;
            if (a.modifiedDate < b.modifiedDate) return 1;
            return 0;
          }

          return bookingList.sort(compare);
        };

        vm.bookingList = sortBooking(vm.bookingList);

        var _loop = function _loop(index) {
          var currentGate = vm.bookingList.filter(function (item) {
            return String(item.gateNumber) === String(vm.gateNumberList[index]['gateNumber']) && !item['speaking'];
          });
          vm.queueList = vm.queueList.concat(currentGate);
        };

        for (var index in vm.gateNumberList) {
          _loop(index);
        }

        if (vm.activeCalling && vm.called && Array.isArray(vm.queueList) && vm.queueList.length > 0) {
          vm.currentCalling = vm.queueList[0];

          try {
            vm.callingApplicant(vm.currentCalling);
          } catch (err) {
            console.log('catch play audio 1'); // vm.getDanhSachCho()
          }
        } else {
          vm.loadData = !vm.loadData;
        }
      } else {
        vm.loadData = !vm.loadData;
      }
    },
    updateStateBooking: function updateStateBooking(item, speakingArg) {
      var vm = this;
      var filter = Object.assign({}, item, {
        speaking: speakingArg
      });
      vm.$store.dispatch('updateBooking', filter).then(function (result) {}).catch(function (reject) {});
    },
    callingApplicant: function callingApplicant(item) {
      var vm = this;

      try {
        if (idDocumentVoicePortlet) {
          vm.idVoicePortlet = idDocumentVoicePortlet;
        }
      } catch (error) {
        console.log('not config');
      }

      vm.updateStateBooking(item, true);

      if (item) {
        vm.called = false;
        $('#audioCalling').html('');
        var numberCalling = String(item['codeNumber']).replace(/[^a-zA-Z0-9]/g, '');
        var srcAudioStart = item['className'] === 'EFORM' ? "/documents/".concat(vm.groupId, "/").concat(vm.idVoicePortlet, "/eformStart1.mp3") : "/documents/".concat(vm.groupId, "/").concat(vm.idVoicePortlet, "/dossierStart1.mp3");
        var numberArr = String(numberCalling).split(''); // 

        var mainGateNumber = '';
        var subGateNumber = '';

        if (isNaN(String(item['gateNumber']).slice(-1))) {
          mainGateNumber = item['gateNumber'].replace(item['gateNumber'].slice(-1), '');
          subGateNumber = item['gateNumber'].slice(-1);
        } // 


        var audioStart = "\n          <audio id=\"start\">\n            <source src=\"".concat(srcAudioStart, "\" type=\"audio/mp3\">\n          </audio>\n        ");
        var audioEnd = "\n          <audio id=\"end\">\n            <source src=\"/documents/".concat(vm.groupId, "/").concat(vm.idVoicePortlet, "/toGateNumber.mp3\" type=\"audio/mp3\">\n          </audio>\n        ");
        var audioNumber = '';

        for (var index = 0; index < numberArr.length; index++) {
          audioNumber += "\n            <audio id=\"au".concat(index, "\">\n              <source src=\"/documents/").concat(vm.groupId, "/").concat(vm.idVoicePortlet, "/").concat(numberArr[index], ".mp3\" type=\"audio/mp3\">\n            </audio>\n          ");
        }

        var gateAudio;

        if (subGateNumber === '') {
          gateAudio = "\n            <audio id=\"gateNumber\">\n              <source src=\"/documents/".concat(vm.groupId, "/").concat(vm.idVoicePortlet, "/").concat(Number(item['gateNumber']), ".mp3\" type=\"audio/mp3\">\n            </audio>\n          ");
        } else {
          gateAudio = "\n            <audio id=\"mainGateNumber\">\n              <source src=\"/documents/".concat(vm.groupId, "/").concat(vm.idVoicePortlet, "/").concat(Number(mainGateNumber), ".mp3\" type=\"audio/mp3\">\n            </audio>\n            <audio id=\"subGateNumber\">\n              <source src=\"/documents/").concat(vm.groupId, "/").concat(vm.idVoicePortlet, "/").concat(String(subGateNumber), ".mp3\" type=\"audio/mp3\">\n            </audio>\n          ");
        }

        $('#audioCalling').html(audioStart + audioNumber + audioEnd + gateAudio); // 

        document.getElementById('start').onended = function () {
          document.getElementById("au0").play();

          var _loop2 = function _loop2(_index) {
            setTimeout(function () {
              document.getElementById("au".concat(_index)).play();
            }, _index * 800);
          };

          for (var _index = 1; _index < numberArr.length; _index++) {
            _loop2(_index);
          }
        };

        document.getElementById("au".concat(numberArr.length - 1)).onended = function () {
          document.getElementById('end').play();
        };

        document.getElementById('end').onended = function () {
          if (!subGateNumber) {
            document.getElementById('gateNumber').play();
          } else {
            document.getElementById('mainGateNumber').play();
            setTimeout(function () {
              document.getElementById('subGateNumber').play();
            }, 600);
          }
        };

        if (!subGateNumber) {
          document.getElementById('gateNumber').onended = function () {
            vm.currentCalling = '';
            vm.called = true;
            vm.loadData = !vm.loadData;
          };
        } else {
          document.getElementById('subGateNumber').onended = function () {
            vm.currentCalling = '';
            vm.called = true;
            vm.loadData = !vm.loadData;
          };
        } // start audio


        setTimeout(function () {
          try {
            document.getElementById('start').play();
          } catch (error) {
            console.log('catch play audio');
            vm.getDanhSachCho();
          }
        }, 300);
      } else {
        vm.called = true;
      }
    }
  }
};