import Vue from 'vue';
import $ from 'jquery';
import toastr from 'toastr';
Vue.use(toastr);
export default {
  props: [],
  components: {},
  data: function data() {
    return {
      groupId: window.themeDisplay.getScopeGroupId()
    };
  },
  computed: {},
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var current = vm.$router.history.current;
      var currentQuery = current.query;
      $('header').css('display', 'none');
      $('#banner').css('display', 'none');
      $('.navbar-container').css('display', 'none');
      $('#footer').css('display', 'none');
      setTimeout(function () {
        $('#footer').css('display', 'none');
      }, 500);
    });
  },
  updated: function updated() {
    var vm = this;
    vm.$nextTick(function () {
      $('header').css('display', 'none');
      $('#banner').css('display', 'none');
      $('.navbar-container').css('display', 'none');
      $('#footer').css('display', 'none');
      setTimeout(function () {
        $('#footer').css('display', 'none');
      }, 500);
    });
  },
  watch: {},
  methods: {
    goTo: function goTo(val) {
      var vm = this;

      if (val === 1) {
        vm.$router.push({
          path: '/lay-so-tu-dong'
        });
      } else if (val === 2) {
        vm.$router.push({
          path: '/danh-sach-cho-tiep-nhan'
        });
      } else if (val === 3) {
        vm.$router.push({
          path: '/dieu-hanh-xep-hang?gate=1,2,3,4'
        });
      } else if (val === 4) {
        vm.$router.push({
          path: '/so-dang-goi?gate=1,2,3,4'
        });
      }
    }
  }
};